import { SensitiveDataComponent } from './sensitive-data/sensitive-data.component';
import { SalesReportComponent } from './sales-report/sales-report.component';

@NgModule({
	declarations: [				
		SalesReportComponent,
		SensitiveDataComponent	
	],
	imports: [
		CommonModule,
		SharedModule,
		MatMenuModule,
		MatCardModule,
		MatCheckboxModule,
		MatDatepickerModule,
		MatNativeDateModule,
		MatDividerModule,
		MatFormFieldModule,
		MatInputModule,
		MatProgressSpinnerModule,
		MatProgressBarModule,
		MatRadioModule,
		MatSelectModule,
		MatPaginatorModule,
		MatTableModule,
		MatProgressSpinnerModule,
		MatProgressBarModule,
		NgxMaskModule.forRoot(),
		ReactiveFormsModule,
		ExportationRoutingModule
	],
	providers: [
		{ provide: DateAdapter, useClass: CustomDateAdapter },
		{ provide: MAT_DATE_FORMATS, useValue: MY_DATE_FORMATS }
	],
	entryComponents: [
		
	]
})
export class ExportationModule { }
