import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { SensitiveDataComponent } from './sensitive-data/sensitive-data.component';

import { ExportationRoutesGuard } from './exportation-routes.guard';
import { SalesReportComponent } from './sales-report/sales-report.component';


const routes: Routes = [		
	{ path: 'dados-sensiveis', component: SensitiveDataComponent, canActivate: [ExportationRoutesGuard] },
	{ path: 'relatorio-vendas', component: SalesReportComponent, canActivate: [ExportationRoutesGuard] }
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule]
})
export class ExportationRoutingModule { }
