import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { ExtractionMassified } from 'src/app/models/extraction-massified.model';
import { ResponseList } from 'src/app/models/response-list.interface';
import { MediaUpload } from 'src/app/models/media-upload.interface';

@Injectable({providedIn: 'root'})
export class ExportationService {
    
    constructor(
        private httpClient: HttpClient,
    ) { }
        
    public exportMassified(body: ExtractionMassified): Observable<any> {
        return this.httpClient.get<any>(`${environment.urlServices}/api/v1/exportacoes/massificada`, {
            params : {
                tipoArquivo : body.tipoArquivo.toString(),
                dataInicial: body.dataInicial,
                dataFinal : body.dataFinal,
                isDownload : 'false'
            }
        })
    }

    public getFileMassified(url: string): Observable<any> {
        return this.httpClient.get<any>(url, {
            responseType : 'blob' as 'json'
        });
    }    
    
    public find(pageIndex: string, pageSize: string, filter: string ): Observable<ResponseList> {
        return this.httpClient.get<ResponseList>(`${environment.urlServices}/api/v1/carga-logs`, {
            params : {
                PageSize: pageSize,
                PageIndex: pageIndex,
                AssemblyName: filter
            }
        })
    }

    public upload(file: any): Observable<any> {
        return this.httpClient.post<Observable<any>>(`${environment.urlServices}/api/v1/shared/upload`, file)
    }	    

    public uploadDadosSensiveis(files: any, document: string): Observable<any> {
        return this.httpClient.post<Observable<any>>(`${environment.urlServices}/api/v1/exportacoes/dados-sensiveis/${document}`, files)
    }     

    public uploadMassiva(file: any): Observable<any> {
        return this.httpClient.post<Observable<any>>(`${environment.urlServices}/upload-massiva`, file)
    }
    
    public getAtividades(): Observable<any> {
        return this.httpClient.get<any>(`${environment.urlServices}/api/v1/atividades`)
    }

    public exportacaoMassiva(file: any): Observable<any> {
        return this.httpClient.post<Observable<any>>(`${environment.urlServices}/api/v1/exportacoes/massiva`, file)
    }
	public exportSalesReport(atividadeId: string, dataInicial: string, dataFinal: string): Observable<any>
	{
		const data = {
			atividadeId: atividadeId,
			dataInicial: dataInicial,
			dataFinal:   dataFinal
        };

        return this.httpClient.post<any>(`${environment.urlServices}/api/v1/exportacoes/relatorio-vendas`, data);
    }

    public uploadPostergar(file: any): Observable<any> {
        return this.httpClient.post<Observable<any>>(`${environment.urlServices}/api/v1/service-center/postergacoes/converter`, file)
    }

    public postergacaoMassiva(itens: any[]): Observable<any> {
        const params = {
            itens : itens
        };
        return this.httpClient.put<Observable<any>>(`${environment.urlServices}/api/v1/service-center/postergacoes/multiplos`, params)
    }	
		

}